import {mapState} from 'vuex'
export default {
	data() {
		return {
			
		}
	},
	computed:{
		...mapState({
			list:state=>state.path.list
		})
	},
	methods: {
		//修改
		toAddPath(index){
			
			let pathObj = JSON.stringify({
				index:index,
				item:this.list[index]
			})
			uni.navigateTo({
				url:"../my-add-path/my-add-path?data="+pathObj+""
			})
			
		},
		//新增
		goAddPath(){
			uni.navigateTo({
				url:"../my-add-path/my-add-path"
			})
		}
	}
}
